import LcTypeService from '@/services/trade/LcTypeService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let LcTypeTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultLcTypeTableFilter: {
      type: String,
      default: null
    },
    defaultLcTypeTableOrderBy: {
      type: String,
      default: null
    },
    typeBehaviourEnumId: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      lcTypeTableData: [],
      lcTypeTableFilterTimer: null,
      lcTypeTableFilter: this.defaultLcTypeTableFilter,
      lcTypeTableOrderBy: this.defaultLcTypeTableOrderBy,
      lcTypeTableContainer: 'loading-container',
      lcTypeTableBusy: false,
      lcTypeTableTotalRows: 0,
      lcTypeTableCurrentPage: 1,
      lcTypeTablePerPage: 10
    }
  },
  computed: {
    lcTypeTableNoDataText: function () {
      return this.lcTypeTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    lcTypeTableTotalPages: function () {
      return Math.ceil(this.lcTypeTableTotalRows / this.lcTypeTablePerPage)
    },
    lcTypeTablePagingInfo: function () {
      return this.pagingInfo(this.lcTypeTableCurrentPage, this.lcTypeTablePerPage, this.lcTypeTableTotalRows)
    }
  },
  methods: {
    loadLcTypeTableData (loadingContainerId) {
      let self = this
      this.lcTypeTableBusy = true
      this.lcTypeTableContainer = loadingContainerId || this.lcTypeTableContainer
      this.startLoading(this.lcTypeTableContainer)
      LcTypeService.lcTypes({
        pageIndex: this.lcTypeTableCurrentPage - 1,
        pageSize: this.lcTypeTablePerPage,
        filter: this.lcTypeTableFilter,
        orderByField: this.lcTypeTableOrderBy,
        typeBehaviourEnumId: this.typeBehaviourEnumId
      }).then(function (response) {
        self.stopLoading(self.lcTypeTableContainer)
        self.lcTypeTableBusy = false
        self.lcTypeTableTotalRows = response.data.totalRows
        self.lcTypeTableData = response.data.lcTypeList || []
      }).catch(function (error) {
        self.stopLoading(self.lcTypeTableContainer)
        self.lcTypeTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshLcTypeTable () {
      this.loadLcTypeTableData()
    },
    filterLcTypeTable (filter) {
      let self = this
      if (this.lcTypeTableFilter !== filter) {
        clearTimeout(this.lcTypeTableFilterTimer)
        this.lcTypeTableFilterTimer = setTimeout(() => {
          self.lcTypeTableFilter = filter
          self.loadLcTypeTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.lcTypeTableFilterTimer)
      }
    },
    orderLcTypeTable (orderBy, active) {
      this.lcTypeTableOrderBy = (active ? '' : '-') + orderBy
      this.loadLcTypeTableData()
    }
  },
  watch: {
    lcTypeTableCurrentPage: function () {
      this.refreshLcTypeTable()
    },
    lcTypeTablePerPage: function () {
      this.refreshLcTypeTable()
    },
    typeBehaviourEnumId: function () {
      this.refreshLcTypeTable()
    }
  }
}

export default LcTypeTableMixin
